import React, { useState, useEffect } from "react"
import Card from '@material-ui/core/Card';
import "./style.css"


export const Entry = ({image, title, dsc, url}) => {
    const [hover, setHover] = useState(false)

    return (
        <a className="card" href={url}>
            <Card 
                onMouseOver={e => (setHover(true))}
                onMouseOut={e => (setHover(false))}
                raised={hover}
            >
                <h1 className="SmallHeader">{title}</h1>
                <img src={image} width="400px" alt="Alt text"/>
                <p>{dsc} </p>
            </Card>
        </a>
    )
}
