import React, { useState, useEffect, useRef } from 'react';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import "./Snake.css"
import chief from "../img/chief.png"

export const Snake = () => {
    let startGrid = []
    const d = 14
    const [dim, setDim] = useState(d)
    for(var i = 0; i < dim; i++){
        startGrid.push([])
        for(var j = 0; j < dim; j++){
        startGrid[i].push(0)
            
        }
    }
    const [grid, setGrid] = useState(startGrid)
    const [f, setF] = useState({x:0, y:0})
    const [player, setPlayer] = useState([{x:0, y:0}])
    const [dir, setDir] = useState({x: 1, y: 0})
    const [rand, setRand] = useState(0)
    const [AI, setAI] = useState(false)
    const [speed, setSpeed] = useState(100)

    const downHandler = (e) => {
        var { keyCode } = e;
        switch(keyCode) {
        case 37:
            e.preventDefault()
            setDir({x:-1, y:0});
            break;
        case 38:
            e.preventDefault()
            setDir({x:0, y:-1});
            break;                   
        case 39:
            e.preventDefault()
            setDir({x:1, y:0});
            break;
        case 40:
            e.preventDefault()
            setDir({x:0, y:1});
            break;
        default:
            break;            
          }
    }

    useEffect(() => {
        setFood()
        document.addEventListener("keydown", downHandler)
    },[])



    const useInterval = (callback, delay) => {
        const savedCallback = useRef();

        // Remember the latest callback.
        useEffect(() => {
          savedCallback.current = callback;
        }, [callback]);

        // Set up the interval.
        useEffect(() => {
          function tick() {
            savedCallback.current();
          }
          if (delay !== null) {
            let id = setInterval(tick, delay);
            return () => clearInterval(id);
          }
        }, [delay]);
      }



    // Sets the food to random pixel
    const setFood = () => {
        let ri = Math.floor(Math.random() * dim)
        let rj = Math.floor(Math.random() * dim)
        let i = 0;
        while (grid[ri][rj] != 0 && i < 1000){
            ri = Math.floor(Math.random() * dim)
            rj = Math.floor(Math.random() * dim)
            i += 1
        }

        let newGrid = grid
        setF({x: rj, y: ri})
        newGrid[ri][rj] = 2
        setGrid(newGrid)

    }

    const move = () => {
        let newPlayer = player
        const head = newPlayer[0]
        let new_x = head.x + dir.x
        let new_y = head.y + dir.y
        if (new_x > dim-1) { new_x = 0 }
        if (new_x < 0) { new_x = dim-1 }
        if (new_y > dim-1) { new_y = 0 }
        if (new_y < 0) { new_y = dim-1 }
        
        let newGrid = grid
        newPlayer.unshift({x:new_x, y: new_y})

        if (! (head.x == f.x && head.y == f.y)){
            const remove = newPlayer.pop()
            newGrid[remove.y][remove.x] = 0
            newGrid[f.y][f.x] = 2
        }
        else{
            setFood()
        }
        newPlayer.forEach(item => 
            newGrid[item.y][item.x] = 1
        )
        if (AI){
            if (newPlayer[0].x < f.x){
                setDir({x: 1, y:0})
            }
            else if (newPlayer[0].x > f.x){
                setDir({x: -1, y:0})
            }

            if (newPlayer[0].y < f.y){
                setDir({x:0, y:1})
            }
            else if (newPlayer[0].y > f.y){
                setDir({x:0, y:-1})
            }
        }

        newGrid[new_y][new_x] = 3
        setGrid(newGrid)
        setPlayer(newPlayer)
        setRand(Math.random())
    }

    // Displays the grid
    const display = grid.map(row => (
        <li className="grid">
            {row.map(item => {
                if (item == 0){
                    return <span className="free"></span>
                }
                else if (item == 2){
                    return <span className="food"></span>
                }
                else if (item == 1){
                    return <span className="player"></span>
                }
                else if (item == 3){
                    return <span className="head"></span>
                }
            })}
        </li>
    ))

    useInterval(move, speed);

    const handleHax = () => {
        setAI(!AI)
        setSpeed(50)
    }
    return(
        <Card className="mainCard">
            <h1 className="SmallHeader">Play To Reveal Picture</h1>
            <Button variant={!AI?"outlined":"contained"} color={AI?"secondary":"primary"} onClick={handleHax}>
                HAX
            </Button>
            
            <div className="strudel">
                <ul className="hax">
                    { display }
                </ul>
            </div>
        </Card>
    )
}
