import Card from '@material-ui/core/Card';
import { Paper, AppBar, Tabs, Tab, TabPanel} from "@material-ui/core"
import React, { useState } from "react";
import { Link } from "react-router-dom"
import "./Menu.css";



export const Menu = () => {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue)
    }

    return (
        <Card className="navBar">
            <Tabs
                value={value}
                indicatorColor="secondary"
                textColor="secondary"
                onChange={handleChange}
                centered
            >
                <Tab style={{fontFamily: "monospace", fontSize:"120%"}} label="About Me"  to='/Education' component={Link} />
                <Tab style={{fontFamily: "monospace", fontSize:"120%"}} label="My Portfolio"  to='/Projects' component={Link} />
                <Tab style={{fontFamily: "monospace", fontSize:"120%"}} label="CV"  to='/CV' component={Link} />
                <Tab style={{fontFamily: "monospace", fontSize:"120%"}} label="Contact"  to='/Contact' component={Link} />
                <Tab style={{fontFamily: "monospace", fontSize:"120%"}} label="Snake"  to='/Snake' component={Link} />
            </Tabs>
        </Card>
    )
}
