import React, { useState } from 'react'
import Card from '@material-ui/core/Card';
import img from "../img/cv.png" 
import { Input } from '@material-ui/core'

export const CV = () => {
    const [password, setPassword] = useState("")

    // Excuse me, you cheeky bastard!? 
    // What are you trying to do?
    String.prototype.hashCode = function() {
        var hash = 0;
        if (this.length == 0) {
            return hash;
        }
        for (var i = 0; i < this.length; i++) {
            var char = this.charCodeAt(i);
            hash = ((hash<<5)-hash)+char;
            hash = hash & hash; 
        }
        return hash;
    }

    return (
        <Card className="mainCard" style={{height: "100vh"}}>
            {password.hashCode() != -1209622523 ? 
                <div>
                    <h1 className="SmallHeader">Enter given key</h1> 
                    <Input 
                        error={password != ""}
                        onChange={e => setPassword(e.target.value)}
                        placeholder="key phrase"
                    />
                </div>
            :
                <div style={{margin: 50}}>
                    <img src={img} alt="alt text" /> 
                </div>
            }
        </Card>
    )
}
